body {
  position        : relative;
  left            : 0px;
  top             : 0px;
  margin          : 0px;
  padding         : 0px;
  background-color: lavender;
  overflow-y      : scroll;
  overflow-x      : hidden;
}

select,
button,
input {
  outline: none;
}

#root {
  position: absolute;
  left    : 0px;
  right   : 0px;
  top     : 0px;
  bottom  : 0px;
}

#header {
  position       : relative;
  margin         : 15px;
  display        : flex;
  align-items    : center;
  justify-content: center;
}

#header a {
  text-decoration: none;
  z-index        : 2;
}

#header h1 {
  text-align: center;
  color     : #ab0000;
  margin    : 0px;
}

#header #theme-container {
  position  : absolute;
  left      : 0px;
  top       : 50%;
  transform : translate(0px, -50%);
  margin-top: 5px;
  color     : #6767a3;
}

#actions>div {
  margin-right: 150px;
}

@media only screen and (max-width: 580px) {
  #header {
    display: table;
    margin : auto;
  }

  #github-img {
    display: none;
  }

  #actions>div {
    margin-right: 0px;
  }

  #header #theme-container {
    position  : relative;
    margin-top: 20px;
    text-align: center;
  }
}

#header #theme-container #theme-selector {
  padding: 2px 8px;
  border : 1px solid #c5c5e9;
  color  : #6767a3;
}

#github-img {
  position: absolute;
  top     : 0px;
  right   : 0px;
  z-index : 2;
}


#actions {
  margin-bottom   : 5px;
  display         : table;
  background-color: #eff0f1;
  padding         : 10px;
  border-radius   : 7px;
  margin          : 10px;
  overflow        : hidden;
}

#actions button {
  float      : left;
  margin     : 2px;
  white-space: nowrap;
}

.log-container {
  position: relative;
}

.log-container::before {
  content    : 'event log';
  position   : absolute;
  top        : 50%;
  left       : 50%;
  transform  : translate(-50%, -50%);
  font-weight: 500;
  color      : #80808075;
}

.log {
  background-color: #eff0f1;
  margin          : 10px;
  color           : #00000080;
  border-radius   : 7px;
  max-height      : 100px;
  overflow        : auto;
}

.log>pre {
  padding: 10px;
  margin : 0px;
}

.clear-log {
  position: absolute;
  right   : 30px;
  top     : 5px;
  cursor  : pointer;
}