.show-code {
    padding-left: 15px;
    font-weight : 100;
    color       : #545454;
}

.show-code+pre {
    background-color: #151515;
    color           : #999;
    border-radius   : 7px;
    margin          : 10px;
    padding         : 10px;
    font-size       : 15px;
    overflow        : auto;
}